@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Poppins', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.dashboard-container{
    width: 100%;
    overflow: hidden;
    /* padding: 10px; */
    box-sizing: border-box;
    /* background-color: rgb(68, 117, 245); */
    text-align: center !important;
}

.dashboard-header h2 {
    font-size: 20px;
    /* pad */
}

.dashboard-header{
    padding: 100px 10px;
    min-height: 400px;
    box-sizing: border-box;
    background-color: rgb(68, 117, 245) ;
}

.dashboard-header p {
    /* width: 100px ; */
    padding: 0 20px;
}
.header-notes{
    display: flex;
    width: 100%;
    justify-content: center;

}
.search{
    width: 100%;
    max-width: 500px;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 10px;
    border: 1px solid white;
    outline: none;  
    margin: 0 10px;
}

.new-note-btn{
    padding: 10px 20px;
    flex-wrap: wrap;
    border: none;
    background-color: white;
    color: rgb(68, 117, 245);
    /* margin-left: -20px; */
    box-sizing: border-box;
    border-radius: 10px;
    outline: none;
    
}

.new-note-btn:hover{
    background-color: rgb(226, 226, 226);
}

.all-notes{
    /* width: 100zz; */
    /* min-height: 100vh; */
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow: auto;
    /* flex-direction: column; */
}
.add-new-note-form{
    width: 300px;
    display: flex;
    margin: 10px;
    flex-direction: column;
}

.title-note,.content-note{
    border: none;
    outline: none;
    background-color: rgb(62, 195, 254);
}

.title-note::-webkit-input-placeholder{
    font-weight: bold;
    color: black;
    font-size: 20px;
}

.title-note:-ms-input-placeholder{
    font-weight: bold;
    color: black;
    font-size: 20px;
}

.title-note::placeholder{
    font-weight: bold;
    color: black;
    font-size: 20px;
}

.title-note{
    padding: 10px;
    font-weight: bold;
    font-size: 20px;
}

.content-note{
    padding: 30px 10px;
    height: 200px;
}

.submit-note-bar{
    border: none;
    transition:ease 1ms ease-in;
    background-color: rgb(54, 171, 221);
    font-weight: bold;
    color: rgb(25, 89, 117);
    text-align: right;
    font-size: 1.2em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.submit-note-bar i:hover{
    color: white;
}

.submit-note-bar i {
    margin: 7px;
}

.date-note{
    font-size: 14px;
    font-weight: normal;
    font-weight: initial;
}

@media (min-width:450px) {
    .all-notes {
        flex-direction: row;
    }
}
.btn-disabled{
    padding: 10px 20px;
    font-weight: bold;
    background-color: grey;
    color: white;
    border: 0;
    border-radius: 20px;
    margin: 10px 0;
}

.btn-submit{
    border: 0;
    width: 90%;
    border-radius: 5px;
    margin: 10px 0;
    padding: 10px 20px;
    font-weight: bold;
    color: white;
    background-color: rgb(68, 117, 245);
}

.login-container{
    width: 300px;
    min-height: 400px;
    position: relative;
    margin: 100px auto;
    border-radius: 20px ;
    overflow: hidden;
    text-align: center;
    padding-top: 30px;
    box-sizing: border-box;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12),
    0px 2px 4px -1px rgba(0, 0, 0, 0.2);
}

.login-container h2{
    margin: 0;
    font-size: 20px;
    color: white;
}

.login-container p{
    font-size: 14px;
    color: white;
    padding: 0 20px;
}


.login-container::after{
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 50%;
    background-color: rgb(68, 117, 245);
}

.login-form{
    border-radius: 10px;
    margin-top: 20px !important;
    width: 80%;
    padding-top: 40px;
    box-sizing: border-box;
    margin: 0 auto;
    background-color: #fff;
    min-height: 200px;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12),
    0px 2px 4px -1px rgba(0, 0, 0, 0.2);
}

.email{
    width: 90%;
    box-sizing: border-box;
    padding: 10px;
}

.password{
    margin-top: 10px;
    width: 90%;
    box-sizing: border-box;
    padding: 10px;
    
}

@media screen and (min-width:450px) {
    .login-container{
        width: 500px;
    }

    .login-form{
        width: 70%;
    }
}
